<template>
  <div
    v-if="overlay"
    class="overlay"
    :class="overlayVisible"
  >
    <div class="overlay-content text-center">
      <div>
        <v-icon
          v-show="!loading && !error && icon && !textLoaderSuccess"
          size="80"
          color="success"
        >
          check_circle_outline
        </v-icon>

        <v-icon
          v-show="!loading && error"
          size="80"
          color="danger"
        >
          error_outline
        </v-icon>
        <v-progress-circular
          v-show="loading"
          indeterminate
          size="50"
          color="primaryLight"
        ></v-progress-circular>
      </div>
      <span
        v-show="loading"
        class="overlay-text"
      >
        {{ textLoader }}
      </span>
      
      <!--<span
        v-show="!loading && !error"
        class="overlay-text overlay-text-success"
        v-html="textLoaderSuccess"
      ></span>-->

      <div
        v-show="!loading && !error"
        class="overlay-text overlay-text-success successDark text-left"
      >
        <div class="icone">
          <v-icon>check</v-icon>
        </div>
        <span class="ml-4 white--text">{{ textLoaderSuccess }}</span>
      </div>
      <!---->

      <span
        v-show="!loading && error"
        class="overlay-text overlay-text-success"
        v-html="textLoaderError"
      ></span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Overlay',
  data: () => ({
    overlay: false,
    loading: false,
    overlayVisible: '',
    textLoader: '',
    textLoaderSuccess: '',
    textLoaderError: '',
    error: false,
    icon: false
  }),
  methods: {
    showOverlay (text = null) {
      this.textLoader = text || 'Aguarde...'
      this.overlay = true
      this.loading = true

      setTimeout (() => {
        this.overlayVisible = 'visible'
      }, 300)
    },
    hideOverlayError (text) {
      this.error = true
      this.textLoaderError = text
      this.hideOverlay(true, false)
    },
    hideOverlaySuccess (text) {
      this.error = false
      this.textLoaderSuccess = text
      this.hideOverlay(true, true)
    },
    hideOverlay (timeout, icon) {
      this.icon = icon
      this.loading = false

      if (timeout) {
        setTimeout(() => {
          this.overlayVisible = ''
        }, 3000)

        setTimeout(() => {
          this.error = false
          this.overlay = false,
          this.loading = false
        }, 4000)
      } else {
        this.overlayVisible = ''
        this.error = false
        this.overlay = false,
        this.loading = false
      } 
    }
  }
}
</script>

<style lang="scss" scoped>
@import 'src/scss/theme.scss';
@import 'src/scss/mixin.scss';

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: block;
  background: transparent;
  z-index: 99999;
  opacity: 0;
  @include transition-mixin(all, 1s);

  &.visible {
    opacity: 1;
    @include transition-mixin(opacity, 1s);
  }

  &:after {
    content: '';
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: .8;
    display: block;
    background: #fff;
  }

  .overlay-content {
    display: block;
    width: max-content;
    height: max-content;
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;

    .overlay-text {
      display: block;
      font-size: 18px;
      position: relative;
      top: 20px;

      &.overlay-text-success {
        @include box-shadown-mixin(.1);
        top: 10px;
        font-size: 22px;
        background: #fff;
        padding: 30px;
        border-radius: 10px;
        min-width: 400px;
        max-width: calc(100% - 30px);
        min-height: 200px;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
}

.icone {
  background: #fff; //#6CD4CA;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  display: flex;
  justify-content: center;
  animation-duration: 3s;
  > i {
    color: #6CD4CA;
    font-size: 40px;
  }
}
</style>